
import baseAdd from 'components/base-add.vue';
import CellFormPreview from 'vux/src/components/cell-form-preview/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'

import overtimeList from 'components/over-time/overtime-list.vue';
import overtimeItem from 'components/over-time/overtime-item.vue';
import {
overtimetQuery
} from '@/api/over-time'
import APP_CONFIGS from '@/utils/app-config'
import Swiper from '../../../static/swiper-3.3.1.min.js'

const overTimeList = (_this) => {
return {
"list": [
{
"date": "8",
"week": "周五",
"type": "正常加班",
"time": "12:00-15:00",
},
{
"date": "8",
"week": "周五",
"type": "正常加班",
"time": "12:00-15:00",
},
{
"date": "8",
"week": "周五",
"type": "正常加班",
"time": "12:00-15:00",
},
]
}
};

const overtimeText = (_this) => {
return {
"list": [
{"hrOverTimeDate": "2017-9-20",},
{"hrOverTimeDate": "2017-9-19",},
]
}
};

export default {
name: 'overTime',
components: {
overtimeList,
overtimeItem,
CellFormPreview,
Group,
Cell,
Datetime,
XTable,
Grid,
GridItem,
baseAdd,
},
data() {
return {
currentDay: 1,
currentMonth: 1,
currentYear: 1970,
currentWeek: 1,
days: [],
overTimeList: '',
overtimeText: overtimeText(this),
value1: "",
timeImg: {
backgroundImage: "url(" + require("../../assets/images/punch-card/time@3.png") + ")",
backgroundRepeat: "no-repeat",
backgroundSize: "100% 100%",
},
chooseday: "",
isShowCol: "",
choseFlag: "",
lbt: [
{},
{},
{},
{},
{},
],
galleryThumbs: "",
galleryTop: "",

}
},

methods: {
swiperBoard () {
let that = this;
let mySwiper = new Swiper('.gallery-top', {
/*  var mySwiper = $('#banner').swiper({*/
spaceBetween: 1,
/* grabCursor: true,*/
initialSlide: 2,
/*  autoplayDisableOnInteraction: true,*///用户操作swiper之后，是否禁止autoplay。默认为true：停止。 如果设置为false，用户操作swiper之后自动切换不会停止，每次都会重新启动autoplay。
loop: true,
loopedSlides: 4,
loopAdditionalSlides: 2,
touchRatio: 0.4,
centeredSlides: true, //设定为true时，活动块会居中，而不是默认状态下的居左。
autoHeight: true,
effect: 'coverflow',
slidesPerView: 1.15,//设置slider容器能够同时显示的slides数量
coverflow: {
rotate: 0,
stretch: -15,
depth: 70,
modifier: 3,
slideShadows: true
},
speed: 500,
iOSEdgeSwipeDetection: true,//设置为true开启IOS的UIWebView环境下的边缘探测。如果拖动是从屏幕边缘开始则不触发swiper。
preventLinksPropagation: true,

onSlidePrevStart: function (swiper) {
if (swiper.activeIndex === 6 || swiper.activeIndex === 5) {
swiper.slideTo(2, 1000, false);
that.pickPre(that.currentYear, that.currentMonth);
} else {
that.pickPre(that.currentYear, that.currentMonth);
}
},


onSlideNextStart: function (swiper) {

if (swiper.activeIndex === 7) {

}
else if (swiper.activeIndex === 8 || swiper.activeIndex === 9) {
swiper.slideTo(2, 5000, false);//activeIndex>9以后数据加载不出来，此方法把activeIndex拉回9以内
that.pickNext(that.currentYear, that.currentMonth);
}
else {
that.pickNext(that.currentYear, that.currentMonth);
}
},
});
},

initData: function (cur) {
let date;
if (cur) {
date = new Date(cur);
} else {
date = new Date();
}
this.currentDay = date.getDate();
this.currentYear = date.getFullYear();
this.currentMonth = date.getMonth() + 1;
this.currentWeek = date.getDay(); // 1...6,0

let currentMonth = date.getMonth();
let nextMonth = ++currentMonth;
let nextMonthFirstDay = new Date(date.getFullYear(), nextMonth, 1);
let oneDay = 1000 * 60 * 60 * 24;

let monthLastDay = new Date(nextMonthFirstDay - oneDay).getDate();

date.setDate(1);
let monthFirstDay = date.getDay();
if (this.currentWeek == 0) {
this.currentWeek = 7;
}
let str = this.formatDate(this.currentYear, this.currentMonth, this.currentDay);
this.days.length = 0;

for (let i = this.currentWeek - 1; i >= 0; i--) {
let d = new Date(str);
d.setDate(d.getDate() - i);
this.days.push(d);
}


if ((monthLastDay === 31 && (monthFirstDay === 0 || monthFirstDay === 6)) || (monthLastDay === 30 && monthFirstDay === 0)) {
for (let i = 1; i <= 42 - this.currentWeek; i++) {
this.isShowCol = true;
let d = new Date(str);
d.setDate(d.getDate() + i);
this.days.push(d);
}
} else {
for (let i = 1; i <= 35 - this.currentWeek; i++) {
this.isShowCol = false;
let d = new Date(str);
d.setDate(d.getDate() + i);
this.days.push(d);
}
}
},


pickPre: function (year, month) {



let d = new Date(this.formatDate(year, month, 1));
d.setDate(0);

this.initData(this.formatDate(d.getFullYear(), d.getMonth() + 1, 1));

this.selectOverTime(this.formatDate(d.getFullYear(), d.getMonth() + 1, 1));

this.value1 = this.value1 = this.formatMonth(d.getFullYear(), d.getMonth() + 1).slice(0, 4) + "年" + this.formatMonth(d.getFullYear(), d.getMonth() + 1).slice(5, 7) + "月";
},


pickNext: function (year, month) {
let d = new Date(this.formatDate(year, month, 1));
d.setDate(35);

this.initData(this.formatDate(d.getFullYear(), d.getMonth() + 1, 1));

this.selectOverTime(this.formatDate(d.getFullYear(), d.getMonth() + 1, 1));

this.value1 = this.formatMonth(d.getFullYear(), d.getMonth() + 1).slice(0, 4) + "年" + this.formatMonth(d.getFullYear(), d.getMonth() + 1).slice(5, 7) + "月";
},


formatDate: function (year, month, day) {
let y = year;
let m = month;
if (m < 10) m = "0" + m;
let d = day;
if (d < 10) d = "0" + d;
return y + "-" + m + "-" + d
},


formatMonth: function (year, month) {
let y = year;
let m = month;
if (m < 10) m = "0" + m;
return y + "-" + m
},

selectOverTime(date){
overtimetQuery({"hrEmpId": 12229, "hrOverTimeDate": date,}).then(response => {
if (response.data.success) {
this.overTimeList = response.data.rows;
} else {
this.overTimeList = [];
}
}).catch(error => {
console.log(error.response);
});
},

aplicationLink(){
this.$router.push("/applyOvertime");
},

overTimeChooseDate(val){
let changeVal = val.slice(0, 4) + "-" + val.slice(5, 7);
let year = new Date(changeVal).getFullYear();
let month = new Date(changeVal).getMonth() + 1;
this.initData(this.formatDate(year, month, 1));
this.selectOverTime(this.formatDate(year, month, 1));
},


getOnDay(day){
/*   alert(day);*/
/* this.choseFlag = index;*/
this.chooseday = new Date(day).getMonth() + "-" + new Date(day).getDate();


setTimeout(() => {
this.loadData();
}, 50);
},


getNowChooseTime(item){
let ListDate = new Date(item.hrOverTimeDate).getMonth() + "-" + new Date(item.hrOverTimeDate).getDate();
if (ListDate === this.chooseday) {
return true;
} else {
return false;
}
},


loadData() {
if (document.getElementById('toDay')) {
let container = document.getElementById('toDay').offsetTop;
let x = this.$refs.my_scroller.scrollTo(0, container, true);
}
},

/* 判断是否是今天*/
isToday(day){
if(day.getFullYear() === new Date().getFullYear() && day.getMonth() === new Date().getMonth() && day.getDate() === new Date().getDate()){
return true;
} else {
return false;
}
},

/*判断是否加班*/
isOvertime(day,itemDate){
if(day.getFullYear() === new Date(itemDate.hrOverTimeDate).getFullYear() &&
day.getMonth() === new Date(itemDate.hrOverTimeDate).getMonth() &&
day.getDate() === new Date(itemDate.hrOverTimeDate).getDate())
{
return true;
}else{
return false;
}
},

/*判断是否今天加班*/
isOvertimeToday(day,itemDate){
if(day.getFullYear() === new Date().getFullYear() &&
day.getMonth() === new Date().getMonth() &&
day.getDate() === new Date().getDate()&&
new Date().getFullYear() === new Date(itemDate.hrOverTimeDate).getFullYear() &&
new Date().getMonth() === new Date(itemDate.hrOverTimeDate).getMonth() &&
new Date().getDate() === new Date(itemDate.hrOverTimeDate).getDate())
{
return true;
}else{
return false;
}
}
},

mounted() {
this.swiperBoard();
},

created: function () {
APP_CONFIGS.getAppToken();
let year = new Date().getFullYear();
let month = new Date().getMonth() + 1;

this.selectOverTime(this.formatDate(year, month, 1));

this.initData(this.formatDate(year, month, 1));

this.value1 = this.formatMonth(year, month).slice(0, 4) + "年" + this.formatMonth(year, month).slice(5, 7) + "月";
},
}




