export default{
  props: ["item"],
  components: {},
  methods: {
      //显示某天
    showDay(date){
    /*  console.log("zhelis--------" + item1.hrOverTimeDate);*/
      let day = new Date(date);
      this.overTimeDay = day.getDate();
    },

   // 显示星期
    showWeek(date){
      let week = new Date(date).getDay();

      switch (week) {
        case 0 :
          this.overTimeWeek = "周日";
          break;
        case 1 :
          this.overTimeWeek = "周一";
          break;
        case 2 :
          this.overTimeWeek = "周二";
          break;
        case 3 :
          this.overTimeWeek = "周三";
          break;
        case 4 :
          this.overTimeWeek = "周四";
          break;
        case 5 :
          this.overTimeWeek = "周五";
          break;
        case 6 :
          this.overTimeWeek = "周六";
          break;
      }

    },
  },
  data () {
    return {
      overTimeDay: '',
      overTimeWeek:'',
    }
  },

  mounted() {
     /* alert("didididi");*/

    /*console.log("zhelis--------" + JSON.stringify(this.item));*/

    let time = this.item;
     this.showDay(time.hrOverTimeDate);
     this.showWeek(time.hrOverTimeDate);
  }

};




